<template>
  <div class="login">
    <div class="logo">
      <img src="../assets/logo.jpg" alt="logo">
    </div>
    <input-group
      type="number"
      v-model="phone"
      placeholder="手机号"
      :btnTitle="btnTitle"
      :disabled="disabled"
      :error="errors.phone"
      @btnClick="getVerifyCode"
    ></input-group>
    <input-group type="number" v-model="verifyCode" placeholder="验证码" :error="errors.code"></input-group>
    <div class="login-des">
      <p>
        新用户登录即自动注册，表示已同意
        <span>《用户服务协议》</span>
      </p>
    </div>
    <div class="login-btn">
      <button :disabled="isClick" @click="loginHandle">登录</button>
    </div>
  </div>
</template>

<script>
import InputGroup from '../components/InputGroup'
export default {
  name: 'login',
  components: {
    InputGroup
  },
  data () {
    return {
      phone: '',
      verifyCode: '',
      errors: {},
      btnTitle: '获取验证码',
      disabled: false
    }
  },
  computed: {
    isClick () {
      if (!this.phone || !this.verifyCode) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    loginHandle () {
      this.errors = {}
      localStorage.setItem('ele_login', true)
      this.$router.push('/')
      // this.$axios
      //   .post('/api/posts/sms_back', {
      //     phone: this.phone,
      //     code: this.verifyCode
      //   })
      //   .then(res => {
      //     // 验证码成功 设置登录状态并跳转
      //     localStorage.setItem('ele-login', true)
      //     this.$router.push('/')
      //   })
      //   .catch(err => {
      //     this.errors = {
      //       code: err.response.data.msg
      //     }
      //   })
    },
    getVerifyCode () {
      if (this.validatePhone()) {
        // 请求验证码
        this.validateBtn()
        // this.$axios()
        this.$axios
          .post('/api/posts/sms_send', {
            tpl_id: '136729',
            key: '795be723dd9e88c3ea98e2b6713ab795',
            phone: this.phone
          })
          .then(res => {
            console.log(res)
          })
      }
    },
    validatePhone () {
      // 判断手机合法
      if (!this.phone) {
        this.errors = {
          phone: '手机号码不能为空'
        }
        return false
      } else if (!/^1[345678]\d{9}$/.test(this.phone)) {
        this.errors = {
          phone: '请填写正确的手机号码'
        }
        return false
      } else {
        this.errors = {}
        return true
      }
    },
    validateBtn () {
      let time = 30
      let timer = setInterval(() => {
        if (time === 0) {
          clearInterval(timer)
          this.btnTitle = '获取验证码'
          this.disabled = false
        } else {
          // 倒计时
          this.btnTitle = `${time}秒后重试`
          this.disabled = true
          time--
        }
      }, 1000)
    }
  }
}
</script>

<style scoped>
.login {
  width: 100%;
  height: 100%;
  padding: 30px;
  box-sizing: border-box;
  background: #fff;
}
.logo {
  text-align: center;
}
.logo img {
  width: 150px;
}
.text-group,
.login-des,
.login-btn {
  margin-top: 20px;
}

.login-des {
  color: #aaa;
  line-height: 22px;
}

.login-des span {
  color: #4d90fe;
}
.login-btn button {
  width: 100%;
  height: 40px;
  background-color: #48ca38;
  border-radius: 4px;
  color: #fff;
  font-size: 14px;
  border: none;
  outline: none;
}
.login-btn button[disabled] {
  background-color: #8bda81;
}
</style>
